import React from 'react'
import s from './Header.module.css';
import { NavLink } from 'react-router-dom';
import { PATH } from '../Routes';

function Header() {
    return (
        <div className={s.header}>
            <div className={s.nav}>
                <NavLink className={s.link} activeClassName={s.activeLink} to={PATH.PRE_JUNIOR}>To preJunior Page</NavLink>
                <NavLink className={s.link} activeClassName={s.activeLink} to={PATH.JUNIOR}>To Junior Page</NavLink>
                <NavLink className={s.link} activeClassName={s.activeLink} to={PATH.UP_JUNIOR}>To upper Junior Page</NavLink>
                <div className={s.toggleNav}></div>
            </div>

        </div>
    )
}

export default Header
