import React from 'react';
import s from './Error404.module.css';

function Error404() {
    return (
        <div className={s.container}>
            <h1>404</h1>
            <h2>Page not found!</h2>
            <div className={s.angryCat}>—ฅ/ᐠ.̫ .ᐟ\ฅ—</div>
        </div>
    )
}

export default Error404
